import React, { useState, useEffect } from 'react';

const NavBar = () => {

  const [currentClick, setCurrentClick] = useState(null);
  const [prevClick, setPrevClick] = useState(null);
  const [firstClick, setEvClick] = useState('active1');

  const onClick = e => { setCurrentClick(e.target.id); }

  useEffect(
    () => {
      if (currentClick !== null) {
        const current = document.getElementById(currentClick);
        current.style.background = '#333';
        current.style.color = '#fff';
        setEvClick('');
      }
      if (prevClick !== null) {
        const prev = document.getElementById(prevClick);
        prev.style.background = '#fff';
        prev.style.color = '#333';
      }
      setPrevClick(currentClick);
    },
    [currentClick],
  );

  // let data = [1, 2, 3, 4, 5];
  // let [btnActive, setBtnActive] = useState("");
  // const toggleActive = (e) => {
  //   setBtnActive((prev) => {
  //     return e.target.value;
  //   });
  // };

  return (
    <div>

      {/* <div id="article_top">
        <div id="top_button">
          {data.map((item, idx) => {
            return (
              <>
                <button
                  value={idx}
                  className={"btn" + (idx == btnActive ? " active" : "")}
                  onClick={toggleActive}
                >
                  {item }
                </button>
              </>
            );
          })}
        </div>
      </div> */}

      <div id="article_top">
        <div id="top_button" >
          <a href="#"><li className={firstClick} id="currntClick1" onClick={onClick}>커뮤니티</li></a>
          <a href="#"><li id="currntClick2" onClick={onClick}>상품문의</li></a>
          <a href="#"><li id="currntClick3" onClick={onClick}>공지사항</li></a>
          <a href="#"><li id="currntClick4" onClick={onClick}>기타문의</li></a>
        </div>
      </div>
    </div>
  );
};

export default NavBar;