import React, { useState } from 'react';

const Community = () => {
  // const [dataNum, setDataNum] = useState('');
  // const [dataTitle, setDataTitle] = useState('');
  const [data, setData] = useState([
    {num: 1, title: '포인트는 어떻게 사용하나요?', article: 0 , writer: '홍길동', date:'2023-01-01'},
    {num: 2, title: '포인트는 어떻게 사용하나요?', article: 0 , writer: '홍길동', date:'2023-01-01'}
  ]); 

  let $title_name = document.querySelector('#title_name');
  let $article_name = document.querySelector('#article_name');

  const handlesubmit = function(e) {
    e.preventDefault();

    let number = data.length + 1;
    let title = $title_name.value;
    let article = $article_name.value;
    let writer = '홍길동';
    let date = '2023-01-01';

    let data_list = {num: number, title: title, article: article, writer: writer, date: date};
    data.push(data_list);

    $title_name.value = '';
    $article_name.value = '';

    if(data.length > 0) {
      post_data_print(1);
      block_print(1);
    }
    $form_post_black.style.display = 'none';
    scrollgo();
  };


  const content_num = 10;
  const block_num = 5;
  const [total_block, setTotal_block] = useState('');
  const [now_block, setNow_block] = useState(1);

  function post_data_print(block) {
    
    let $post_list = document.querySelectorAll('.post_row');
    $post_list.forEach( item => item.remove() );

    let start_content = (data.length - 1) - content_num * (block - 1);

    for(let i = start_content; i >= 0 && i > start_content - content_num; i--) {
      let $notice_board = document.querySelector('#notice_board');
      let $post = document.createElement('ul');
      $post.className = 'post_row';

      let post_data = [data[i].num, data[i].title, data[i].writer, data[i].date];
      let li_classname = ['board_top1', 'board_top2', 'board_top3', 'board_top4'];

      for(let j = 0; j < li_classname.length; j++) {
        let li = document.createElement('li');
        li.className = li_classname[j];
        li.textContent= post_data[j];
        $post.appendChild(li);
      }

      let $delete_button = document.createElement('button');
      $delete_button.className = 'delete_button';
      $delete_button.textContent = '삭제하기';
      $post.appendChild($delete_button);
      $delete_button.addEventListener('click', () => {
        data.splice(i, 1);
        post_data_print(1);
      })

      $notice_board.appendChild($post);
    }
  }


  function block_print(front_block) {
    setNow_block(front_block);
    setTotal_block(Math.ceil(data.length / content_num));

    if(front_block <= 1) {
      document.querySelector('.before_move').disabled = true;
    } else {
      document.querySelector('.before_move').disabled = false;
    }
    if(front_block + block_num >= total_block) {
      document.querySelector('.next_move').disabled = true;
    } else {
      document.querySelector('.next_move').disabled = false;
    }

    let $block_box = document.querySelector('.block');
    $block_box.replaceChildren();

    for(let i = front_block; i < front_block + block_num; i++) {
      let $button = document.createElement('button');
      $button.textContent = i;
      $button.className = 'button_down';
      $button.addEventListener('click', (e) => {
        let event = e.target;
        Array.from($block_box.children).forEach(item => {
          item.style.backgroundColor = '#fff';
          item.style.color = '#9e9e9e';
          if(event) {
            event.style.backgroundColor = '#c2c1c1';
            event.style.color = '#333';
          }
        })
        post_data_print(i);
      });
      $block_box.appendChild($button);
    }
  }


  const beforeMove = () => {
    block_print(now_block - block_num);
  }
  const nextMove = () => {
    block_print(now_block + block_num);
  }

  window.onload = function() {
    post_data_print(1);
    block_print(1);
  }

  // function topmove() {
  //   window.scrollTo(0, 0);
  // }

  function scrollstop() {
    document.body.classList.add("stopscroll"); 
  }
  function scrollgo() {
    document.body.classList.remove("stopscroll"); 
  }

  let $form_post_black = document.querySelector('#form_post_black');

  const writeClick = () => {
    $form_post_black.style.display = 'block';
    scrollstop();
  }
  const cancleClick = () => {
    $form_post_black.style.display = 'none';
    scrollgo();
    $title_name.value = '';
    $article_name.value = '';
  }


  return (
    <div>
      <div id="form_post_black">
        <div id="form_post">
          <div id="top_text">
            <div>커뮤니티</div>
            <div>상품 커뮤니티 입니다.</div>
          </div>
          <form id="my_form" onSubmit={handlesubmit}>
            <div><span>제목 :</span><input type="text" id="title_name" name="title_name" autoComplete="off" /></div>
            <div>글쓴이 : <span>홍길동</span></div>
            <div>내용 :</div>
            <div><textarea type="text" id="article_name" name="article_name" ></textarea></div>
            <br />
              <button>완료</button>
              <div id="cancle_button">취소</div>
              {/* <div id="cancle_button" onClick={() => {cancleClick()}}>취소</div> */}
          </form>
        </div>
      </div>

      <div id="article_main">
        <div id="article_top">
          <div id="top_text">
            <div>커뮤니티</div>
            <div>상품 커뮤니티 입니다.</div>
          </div>
          <div id="post_write"><a href="#">글쓰기</a></div>
          {/* <div id="post_write" onClick={() => {writeClick()}}><a href="#">글쓰기</a></div> */}
        </div>


        <div id="notice_board">
          <ul className="board_row title_row">
            <li className="board_top1">번호</li>
            <li className="board_top2">제목</li>
            <li className="board_top3">작성자</li>
            <li className="board_top4">작성일</li>
            <li className="board_top5">비고</li>
          </ul>
          <div className="virtual1"></div>
          <div className="virtual2"></div>
          <div className="virtual3"></div>
          <div className="virtual4"></div>
          <div className="virtual5"></div>
          <div className="virtual6"></div>
          <div className="virtual7"></div>
          <div className="virtual8"></div>
          <div className="virtual9"></div>
          <div className="virtual10"></div>
        </div>

        <div id="pagination">
          <button className="before_move" onClick={() => {beforeMove()}}>이전</button>
          <div className="block">

          </div>
          <button className="next_move" onClick={() => {nextMove()}}>다음</button>

        </div>
      </div>
    </div>
  );
};

export default Community;